<template><section><h1>EllipseGraphics</h1>
<p><code v-pre>vc-graphics-ellipse</code> 组件用于加载椭圆或者由椭圆拉伸的椭圆体。需要作为 <code v-pre>vc-entity</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>加载椭圆、椭圆体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity ref="entity1" :position="position1" :description="description" :ellipse.sync="ellipse1">
          <vc-graphics-ellipse :semiMinorAxis="300000.0" :semiMajorAxis="300000.0" :height="200000.0" :material="material1" :outline="true"></vc-graphics-ellipse>
        </vc-entity>
        <vc-entity ref="entity2" :position="position2" :description="description" :ellipse.sync="ellipse2">
          <vc-graphics-ellipse :semiMinorAxis="250000.0" :semiMajorAxis="400000.0" :material="material2"></vc-graphics-ellipse>
        </vc-entity>
        <vc-entity ref="entity3" :position="position3" :description="description" :ellipse.sync="ellipse3">
          <vc-graphics-ellipse :semiMinorAxis="150000.0" :semiMajorAxis="300000.0" :extrudedHeight="200000.0" :rotation="rotation3" :material="material3" :outline="true"></vc-graphics-ellipse>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity1"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position1"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:ellipse.sync</span>=<span class="hljs-string">"ellipse1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-ellipse</span>
          <span class="hljs-attr">:semiMinorAxis</span>=<span class="hljs-string">"300000.0"</span>
          <span class="hljs-attr">:semiMajorAxis</span>=<span class="hljs-string">"300000.0"</span>
          <span class="hljs-attr">:height</span>=<span class="hljs-string">"200000.0"</span>
          <span class="hljs-attr">:material</span>=<span class="hljs-string">"material1"</span>
          <span class="hljs-attr">:outline</span>=<span class="hljs-string">"true"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-ellipse</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity2"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position2"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:ellipse.sync</span>=<span class="hljs-string">"ellipse2"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-ellipse</span> <span class="hljs-attr">:semiMinorAxis</span>=<span class="hljs-string">"250000.0"</span> <span class="hljs-attr">:semiMajorAxis</span>=<span class="hljs-string">"400000.0"</span> <span class="hljs-attr">:material</span>=<span class="hljs-string">"material2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-ellipse</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity3"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position3"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:ellipse.sync</span>=<span class="hljs-string">"ellipse3"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-ellipse</span>
          <span class="hljs-attr">:semiMinorAxis</span>=<span class="hljs-string">"150000.0"</span>
          <span class="hljs-attr">:semiMajorAxis</span>=<span class="hljs-string">"300000.0"</span>
          <span class="hljs-attr">:extrudedHeight</span>=<span class="hljs-string">"200000.0"</span>
          <span class="hljs-attr">:rotation</span>=<span class="hljs-string">"rotation3"</span>
          <span class="hljs-attr">:material</span>=<span class="hljs-string">"material3"</span>
          <span class="hljs-attr">:outline</span>=<span class="hljs-string">"true"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-ellipse</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">ellipse1</span>: {},
        <span class="hljs-attr">position1</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">111.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">150000.0</span> },
        <span class="hljs-attr">material1</span>: <span class="hljs-string">'GREEN'</span>,

        <span class="hljs-attr">ellipse2</span>: {},
        <span class="hljs-attr">position2</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">103.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span> },
        <span class="hljs-attr">material2</span>: <span class="hljs-literal">undefined</span>,

        <span class="hljs-attr">ellipse3</span>: {},
        <span class="hljs-attr">position3</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">95.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">100000.0</span> },
        <span class="hljs-attr">rotation3</span>: <span class="hljs-number">0</span>,
        <span class="hljs-attr">material3</span>: {}
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([<span class="hljs-keyword">this</span>.$refs.entity1.createPromise, <span class="hljs-keyword">this</span>.$refs.entity2.createPromise, <span class="hljs-keyword">this</span>.$refs.entity3.createPromise]).then(
        <span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
          instances[<span class="hljs-number">0</span>].viewer.zoomTo(instances[<span class="hljs-number">0</span>].viewer.entities)
        }
      )
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.material2 = Cesium.Color.RED.withAlpha(<span class="hljs-number">0.5</span>)

        <span class="hljs-keyword">this</span>.material3 = Cesium.Color.BLUE.withAlpha(<span class="hljs-number">0.5</span>)
        <span class="hljs-keyword">this</span>.rotation3 = Cesium.Math.toRadians(<span class="hljs-number">45</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 ellipse 是否显示。</td>
</tr>
<tr>
<td>semiMajorAxis</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 ellipse 长半轴。</td>
</tr>
<tr>
<td>semiMinorAxis</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 ellipse 短半轴。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 ellipse 高度。</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 ellipse 高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 ellipse 拉伸高度。</td>
</tr>
<tr>
<td>extrudedHeightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 ellipse 拉伸高度模式。<strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定 ellipse 正北逆时针旋转角度。</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定 ellipse 纹理正北逆时针旋转角度。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定每个经纬度之间的采样粒度。</td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 ellipse 是否填充材质。</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre>'white'</code></td>
<td><code v-pre>optional</code> 指定 ellipse 材质。</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定 ellipse 是否绘制轮廓线。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre>'black'</code></td>
<td><code v-pre>optional</code> 指定 ellipse 轮廓线颜色。</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定 ellipse 轮廓线宽度。</td>
</tr>
<tr>
<td>numberOfVerticalLines</td>
<td>Number</td>
<td><code v-pre>16</code></td>
<td><code v-pre>optional</code> 指定 ellipse 沿轮廓周长绘制的垂直线数。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 ellipse 是否投射接收每一个光源的阴影。<strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 ellipse 随相机距离的显示条件。 <strong>结构：{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>classificationType</td>
<td>Number</td>
<td><code v-pre>2</code></td>
<td><code v-pre>optional</code> 指定 ellipse 的贴地模式。 <strong>TERRAIN: 0, CESIUM_3D_TILE: 1, BOTH: 2, NUMBER_OF_CLASSIFICATION_TYPES: 3</strong></td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 ellipse 顺序，没有高度和拉伸高度才有效。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/EllipseGraphics.html">EllipseGraphics</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data () {
        return {
          description: 'Hello Vue Cesium',
          ellipse1: {},
          position1: { lng: 111.0, lat: 40.0, height: 150000.0 },
          material1: 'GREEN',

          ellipse2: {},
          position2: { lng: 103.0, lat: 40.0 },
          material2: undefined,

          ellipse3: {},
          position3: { lng: 95.0, lat: 40.0,  height: 100000.0 },
          rotation3: 0,
          material3: {}
        }
      },
      mounted() {
        Promise.all([this.$refs.entity1.createPromise, this.$refs.entity2.createPromise, this.$refs.entity3.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
          this.material2 = Cesium.Color.RED.withAlpha(0.5)

          this.material3 = Cesium.Color.BLUE.withAlpha(0.5)
          this.rotation3 = Cesium.Math.toRadians(45)
        }
      }
    }
  </script>